import React, { ReactElement } from 'react';
import { NavLink } from 'react-router-dom';
import classNames from 'classnames/bind';
import Logo from '../../../../assets/logo.component.svg';
import NavIcon1 from '../../../../assets/NavBadge-1.component.svg';
import NavIcon2 from '../../../../assets/NavBadge-2.component.svg';
import NavIcon3 from '../../../../assets/NavBadge-3.component.svg';
import styles from './styles.module.scss';

const cx = classNames.bind(styles);

type NavItem = {
  name: string,
  href: string,
  id: number,
  icon: ReactElement,
};

const navItems: NavItem[] = [
  {
    name: 'Карточка автомобиля',
    href: '/fuck',
    id: 1,
    icon: <NavIcon1 />,
  },
  {
    name: 'Список авто',
    href: '/222',
    id: 2,
    icon: <NavIcon2 />,
  },
  {
    name: 'Заказы',
    href: '/',
    id: 3,
    icon: <NavIcon3 />,
  }
];

interface ISidebarProps {
  className?: string;
}

const Sidebar = ({ className }: ISidebarProps): React.ReactElement => (
    <div
      className={cx(
        'navigation',
        className,
      )}
    >
      <div className={styles['navigation__logo']}>
        <Logo />
        Need for car
      </div>
      <nav className={styles['navigation__menu']}>
        {navItems.map((item) => (
          <NavLink
            className={cx(
              'nav-item',
              ({ isActive }) => isActive ? 'nav-item-selected' : '',
            )}
            key={item.id}
            to={item.href}
          >
            {item.icon}
            <p>{item.name}</p>
          </NavLink>
        ))}
      </nav>
    </div>
  )
;

export default Sidebar;
